.calculator {
  background-color: #fff;
  border-radius: 20px;
  padding: 20px;
  width: 400px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.display {
  background-color: #200a5a;
  height: 300px;
  color: #fff;
  font-size: 24px;
  padding: 10px;
  text-align: right;
  border-radius: 10px;
  margin-bottom: -7px;
}

.keypad {
  background-color: #111111;
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.left-side {
  flex: 3;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
}

.left-side button {
  background-color: #111111;
  width: 100%;
  margin-bottom: 10px;
  border: none;
  border-radius: 10px;
  padding: 15px;
  font-size: 18px;
  color: #fff;
  /* background-color: #007bff; */
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.right-side {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.right-side button {
  background-color: #111111;
  width: 100%;
  margin-bottom: 10px;
  border: none;
  border-radius: 10px;
  padding: 15px;
  font-size: 18px;
  color: #fff;
  /* background-color: #007bff; */
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
